nav .nav-link::after {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    /* background-color: #fff; */
    left: 0;
    bottom: 0;
    transition: width 0.3s ease;
    border-radius: 5rem;
}

nav .nav-link:hover::after, nav .nav-link-active::after {
    width: 100%;
    left: 0;
}

